import React from "react";
import axios from "axios";
import { toast } from "react-toastify";

class ChangePassword extends React.Component {
  state = { oldPassword: "", newPassword: "" };

  handleChangePassword = async (event) => {
    event.preventDefault();
    const { oldPassword, newPassword } = this.state;

    if (!oldPassword || !newPassword) {
      toast.warning("⚠️ Vui lòng nhập đầy đủ mật khẩu cũ và mới");
      return;
    }

    try {
      const token = sessionStorage.getItem("token");
      await axios.post(
        `${process.env.REACT_APP_URL_API_SERVER}/changepassword`,
        { oldPassword, newPassword },
        { headers: { Authorization: `Bearer ${token}` } }
      );

      toast.success("✅ Đổi mật khẩu thành công");
      this.setState({ oldPassword: "", newPassword: "" });
    } catch (error) {
      toast.error("❌ Đổi mật khẩu thất bại");
      console.error(error.response?.data || error.message);
    }
  };

  render() {
    return (
      <div
        className="d-flex justify-content-center align-items-center"
        style={{
          minHeight: "100vh",
          background: "#f7f7f7",
          marginTop: "100px",
        }}
      >
        <div
          className="card shadow"
          style={{ width: "100%", maxWidth: "500px" }}
        >
          <div className="card-body">
            <h4 className="card-title mb-4 text-center">🔒 Change password</h4>
            <form onSubmit={this.handleChangePassword}>
              <div className="mb-3">
                <label className="form-label">Current Password</label>
                <input
                  type="password"
                  value={this.state.oldPassword}
                  onChange={(e) =>
                    this.setState({ oldPassword: e.target.value })
                  }
                  className="form-control"
                  placeholder="Enther current password..."
                  required
                />
              </div>

              <div className="mb-4">
                <label className="form-label">New password</label>
                <input
                  type="password"
                  value={this.state.newPassword}
                  onChange={(e) =>
                    this.setState({ newPassword: e.target.value })
                  }
                  className="form-control"
                  placeholder="Enter new password..."
                  required
                />
              </div>

              <div className="d-grid">
                <button className="btn btn-primary" type="submit">
                  ✅ Update password
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

export default ChangePassword;
